body {
    padding: 0;
    margin: 0;

    &::-webkit-scrollbar {
        width: $scroll-bar-width;
        background-color: $co-scrollbar-bg;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $co-scrollbar-thumb;
        border-radius: $br-xs;
    }
}

h1 {
    font-size: $fs-h1;
    font-family: $ff-header;

    @media (max-width: $bp-sm) {
        font-size: $fs-h2;
    }
}

h2 {
    font-size: $fs-h2;
    font-family: $ff-header;

    @media (max-width: $bp-sm) {
        font-size: $fs-h4;
    }
}

h3 {
    font-size: $fs-h3;
    font-family: $ff-header;

    @media (max-width: $bp-sm) {
        font-size: $fs-h4;
    }
}   

h4 {
    font-size: $fs-h4;
    font-family: $ff-text;

    @media (max-width: $bp-sm) {
        
    }
}

h5 {
    font-size: $fs-lg;
    font-family: $ff-header;

    @media (max-width: $bp-sm) {
        font-size: $fs-md;
    }
}

p {
    font-size: $fs-md;
    font-family: $ff-text;

    @media (max-width: $bp-sm) {
        font-size: $fs-md;
    }
}

section {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-y: hidden;
}

.container-lg {
    display: flex;
    align-items: center;
    max-width: $bp-lg;
    width: calc(100% - 2*$sp-md);
    padding: $he-xxl $sp-md;

    @media (max-width: $bp-md) {
        padding: $he-lg $sp-md;
        width: calc(100% - 2*$sp-md);
    }

    @media (max-width: $bp-sm) {
        padding: $he-md $sp-md;
        width: calc(100% - 2*$sp-md);
    }
}

.col-2 {
    width: 50%;

    @media (max-width: $bp-md) {
        width: 100%;
    }
}

.section-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: $sp-sm;
    margin-bottom: 60px;
    width: 100%;

    h4 {
        font-family: $ff-header;
        font-size: $fs-h4;
        color: $co-header;
        margin-top: 0;
        margin-bottom: $sp-sm;
    }

    p {
        font-family: $ff-text;
        font-size: $fs-md;
        text-align: center;
        color: $co-text;
        font-weight: $fw-normal;
        margin: 0;
    }
}
