@import "../../styles/main.scss";

footer {
    background-color: $co-primary;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .footer-container {
        height: 100%;
        max-width: $bp-sm;
        width: calc(100% - 2*$sp-md);
        display: flex;
        justify-content: space-between;
        padding: $sp-md;

        .footer-left {
            display: flex;
            flex-direction: column;
            gap: $sp-xs;

            .footer-logo {
                height: $he-sm;
                cursor: pointer;
            }

            .footer-social-icons {
                display: flex;
                gap: $sp-sm;
                padding: 0;
                margin: 0;

                li {
                    list-style: none;

                    a {
                        color: $co-body-bg;
                        transition: color $td-3 linear;

                        &:hover {
                            color: $co-secondary;
                        }
                    }
                }
            }
        }

        .footer-right {
            display: flex;
            justify-content: center;
            align-items: center;

            .footer-nav {
                display: flex;
                justify-content: center;
                gap: $sp-sm;
                margin: 0;
                cursor: pointer;

                a {
                    text-decoration: none;
                    list-style: none;
                    color: white;
                    font-family: $ff-header;
                    font-size: $fs-md;
                    font-weight: $fw-thin;
                }

                @media screen and (max-width: $bp-sm) {
                    flex-direction: column;
                    cursor: none;
                }
            }
        }
    }
}