@import '../../styles/main';

nav {
    display: flex;
    position: fixed;
    height: $he-xxl;
    top: 0;
    left: 0;
    right: 0;
    align-items: center;
    justify-content: center;
    background-color: $co-nav-bg;
    z-index: 999;
    transition: box-shadow $td-3 linear;

    &.scrolled {
        box-shadow: $bs-sm;
        transition: box-shadow $td-3 linear;
    }

    .nav-container {
        width: 100%;
        max-width: $bp-lg;
        height: $he-xxl;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 $sp-md;

        .nav-logo {
            height: $he-sm;

            img {
                height: $he-sm;
            }
        }

        .menu-toggle {
            height: $sp-md;
            width: $sp-md;
            position: relative;
            display: none;
            @include transition(transform, $td-5 ease-in-out);
            cursor: pointer;

            &:hover {
                color: $co-toggle;
            }

            span,
            span:after,
            span:before {
                display: block;
                position: absolute;
                height: 2px;
                width: $sp-md;
                right: 0;
                background: $co-toggle;
                border-radius: $br-xs;
                @include transition(transform, $td-3 ease-in-out);
            }

            span:after,
            span:before {
                content: '';
            }

            span:after {
                top: -16px;
            }

            span:before {
                top: -8px;
            }

            span {
                top: 16px;
            }

            &.open span:after {
                transform: rotate(45deg);
                top: -13px;
            }

            &.open span:before {
                transform: rotate(-45deg);
                top: -13px;
            }
        }

        .nav-items {
            display: flex;
            padding: 0;

            .active a,
            .active a .nav-items-icon {
                color: black;
                font-weight: $fw-semi;
            }

            li {
                display: flex;
                color: $co-text-hover;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                gap: $sp-xs;
                list-style: none;
                margin: 0 $sp-xs;
                cursor: pointer;
                font-family: $ff-text;

                a {
                    color: $co-text;
                    text-decoration: none;
                    font-weight: $fw-normal;
                    @include transition(color, $td-3);
                }

                .nav-items-icon {
                    display: none;
                    color: $co-text;
                }
            }
        }

        .nav-social-icons {
            display: flex;
            padding: 0;

            li {
                list-style: none;
                justify-content: center;
                align-items: center;
                text-align: center;

                a {
                    color: $co-text;
                    margin: 0 $sp-xs;
                    @include transition(color, $td-3);

                    &:hover {
                        color: $co-text-hover;
                    }
                }
            }
        }

        @media (max-width: $bp-sm) {
            padding: 0;

            .nav-logo {
                margin-left: $sp-md;
            }

            .nav-items {
                display: none;

                &.open {
                    display: flex;
                    justify-content: space-evenly;
                    position: absolute;
                    width: 100%;
                    top: calc(#{$he-xxl} - 1px);
                    background-color: $co-body-bg;
                    margin: 0;
                    border-bottom: 1px solid $co-footer-bg;
                    border-radius: 0 0 $br-xl $br-xl;
                    padding: $sp-sm 0;

                    li a {
                        display: flex;
                        flex-direction: column;
                        gap: $sp-xs;
                        justify-content: center;
                        align-items: center;
                    }

                    .nav-items-icon {
                        display: flex;
                    }
                }
            }

            .menu-toggle {
                display: block;
                margin-right: $sp-md;
            }

            .nav-social-icons {
                display: none;
            }

            .section-home {
                flex-direction: column-reverse;
            }
        }

        @media screen and (max-width: $bp-xs) {
            .nav-items.open {
                display: grid;
                grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
                grid-gap: $sp-md;
            }
        }
    }
}