@import "../../styles/main.scss";

.scroll-to-top-button {
    position: fixed;
    bottom: $sp-md;
    right: $sp-md;
    display: none;

    &.visible {
        transition: opacity $td-5 linear;
        display: block;
    }

    button {
        background-color: $co-secondary;
        color: white;
        border: none;
        opacity: 0.6;
        border-radius: $br-md;
        padding: $sp-sm $sp-sm;
        cursor: pointer;
        transition: opacity $td-3 ease-in-out;

        &:hover {
            opacity: 1;
        }

        @media (max-width: $bp-md) {
            opacity: 1;
            cursor: none;
        }
    }
}