@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

/* =========== COLORS =========== */
$co-primary: #161927;
$co-secondary: #ffbd59;
$co-body-bg: #fbfbff;
$co-nav-bg: $co-body-bg;
$co-toggle: #999;
$co-footer-bg: $co-primary;
$co-text: #4f4f4f;
$co-text-hover: #4b4b4b;
$co-text-title: #00505c;
$co-header: #232323;
$co-shadow: #0000001a;
$co-icons: #131313;
$co-scrollbar-bg: #e1e8fa;
$co-scrollbar-thumb: #434c60;

/* =========== FONTS =========== */
$ff-header: 'Poppins', sans-serif;
$ff-text: 'Montserrat', sans-serif;

// Size
$fs-xs: 12px;
$fs-sm: 14px;
$fs-md: 16px;
$fs-lg: 18px;
$fs-xl: 24px;

$fs-h5: 30px;
$fs-h4: 34px;
$fs-h3: 48px;
$fs-h2: 56px;
$fs-h1: 82px;

// Weight
$fw-thin: 300;
$fw-normal: 400;
$fw-semi: 600;
$fw-bold: 700;

// Line Height
$lh-sm: 10px;
$lh-md: 15px;
$lh-lg: 20px;
$lh-xl: 25px;

/* =========== SPACING =========== */
$sp-xxs: 6px;
$sp-xs: 10px;
$sp-sm: 15px;
$sp-md: 20px;
$sp-lg: 30px;
$sp-xl: 40px;
$sp-xxl: 50px;

/* =========== HEIGHTS =========== */
$he-xxs: 20px;
$he-xs: 25px;
$he-sm: 30px;
$he-md: 40px;
$he-lg: 50px;
$he-xl: 65px;
$he-xxl: 70px;

/* =========== BORDERS =========== */
$bo-xs: 1px solid #ccc;
$bo-sm: 2px solid #ccc;
$bo-md: 3px solid #ccc;
$bo-lg: 4px solid #ccc;
$bo-xl: 5px solid #ccc;

// Radius
$br-xs: 3px;
$br-sm: 6px;
$br-md: 8px;
$br-lg: 10px;
$br-xl: 12px;

/* =========== SHADOWS =========== */
$bs-xs: 0 1px 2px $co-shadow;
$bs-sm: 0 2px 4px $co-shadow;
$bs-md: 0 4px 6px $co-shadow;
$bs-lg: 0 10px 15px $co-shadow;
$bs-xl: 0 20px 25px $co-shadow;

/* =========== TRANSITIONS =========== */
$td-3: 0.3s;
$td-5: 0.5s;

/* =========== BREAKPOINTS =========== */
$bp-xs: 480px;
$bp-sm: 768px;
$bp-md: 1024px;
$bp-lg: 1200px;
$bp-xl: 1600px;

/* =========== SCROLL-BAR =========== */
$scroll-bar-width: 15px;