@import '../../styles/main.scss';

.about {
    flex-direction: column;
    width: 100%;

    .about-container {
        display: flex;
        gap: $sp-md;

        .about-photo {
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
            text-align: center;
            gap: $sp-lg;

            .photo {
                width: calc(100% - 40px);
                max-width: 400px;
            }

            .about-social {
                display: flex;
                align-items: center;
                gap: $sp-xs;

                .social-icons {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 40px;
                    height: 40px;
                    border: 1px solid $co-icons;
                    border-radius: 50%;
                    color: $co-icons;
                    background-color: $co-body-bg;
                    cursor: pointer;
                    transition: transform $td-3, box-shadow $td-3;
                    box-shadow: $bs-sm;

                    &:hover {
                        transform: translateY(-5px);
                        box-shadow: $bs-md;
                    }
                }

                .btn-cv {
                    display: flex;
                    align-items: center;
                    gap: $sp-xs;
                    border: 1px solid $co-primary;
                    border-radius: 4px;
                    padding: $sp-xs $sp-sm;
                    background-color: $co-body-bg;
                    cursor: pointer;
                    transition: transform $td-3, box-shadow $td-3;
                    box-shadow: $bs-sm;
                    font-family: $ff-text;
                    font-weight: $fw-semi;
                    
                    a {
                        font-size: $fs-sm;
                        color: $co-primary !important;
                        text-decoration: none;
                    }

                    &:hover {
                        transform: translateY(-5px);
                        box-shadow: $bs-md;
                    }
                }
            }
        }

        .about-desc {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            p {
                line-height: $sp-lg;
                color: $co-text;
            }
        }
    }

    @media screen and (max-width: $bp-md) {
        .about-container {
            display: flex;
            flex-direction: column;
        }
    }
}