@import "../../../styles/main.scss";

.project-card {
    display: flex;
    flex-direction: column;
    border-radius: $br-xl;
    box-shadow: $bs-md;
    transition: transform $td-3, box-shadow $td-3;
    background-color: white;
  
    &:hover {
      box-shadow: $bs-lg;
      transform: translateY(-5px);
    }
  
    .card-img {
      object-fit: contain;

      @media (max-width: $bp-md) {
        width: 100%;
      }
    }
  
    .card-container {
      padding: $sp-md;
  
      .card-title {
        font-family: $ff-text;
        font-size: $fs-xl;
        color: $co-header;
        margin: 0;
      }
  
      .card-description {
        font-size: $fs-sm;
        line-height: $lh-lg;
        font-family: $ff-text;
        color: $co-text-hover;
      }
  
      .card-icons {
        display: flex;
        gap: $sp-xs;
        flex-wrap: wrap;
  
        .card-chip {
          border: $bo-xs;
          border-radius: $br-xl;
          width: fit-content;
          margin: $sp-xs 0;
  
          p {
            margin: 0;
            padding: $sp-xxs $sp-xs;
            font-family: $ff-text;
            font-weight: $fw-bold;
            font-size: $fs-xs;
            color: $co-header;
          }
        }
      }
  
      .card-links {
        display: flex;
        margin-top: $sp-md;
        justify-content: right;
        align-items: center;
        gap: $sp-sm;
  
        a {
          color: $co-text-hover;
          transition: color $td-3 linear;
  
          &:hover {
            color: $co-text;
          }
        }
      }
    }
  }
  