@import '../../styles/main.scss';

section {
  .projects {
    flex-direction: column;
    justify-content: center;

    .projects-container {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
      grid-gap: $sp-lg;
      width: 100%;

      @media screen and (max-width: $bp-sm) {
        padding: 0;
        grid-template-columns: 1fr;
      }
    }

    .projects-more {
      display: flex;
      width: calc(100% - 60px);
      justify-content: center;
      align-items: flex-end;
      padding: $sp-lg;
      text-align: right;

      p {
        display: flex;
        align-items: center;
        gap: $sp-xxs;
        font-size: $fs-sm;
        color: $co-text;
        font-weight: $fw-semi;
        transition: color $td-3 linear;
        cursor: pointer;

        &:hover {
          color: black;

          .arrow {
            transform: translateX(5px);
            transition: transform $td-3 linear;
          }
        }
      }
    }
  }
}