@import "../../styles/main.scss";

section {
    .contact {
        flex-direction: column;

        .section-container {
            display: flex;
            width: 100%;
            margin-bottom: 150px;
            justify-content: center;
            align-items: center;
            gap: $sp-lg;

            .contact-form {
                width: 100%;

                form {
                    display: flex;
                    flex-direction: column;

                    .container-contact {
                        display: flex;
                        gap: $sp-md;

                        @media (max-width: $bp-sm) {
                            gap: 0;
                        }

                        .contact-left {
                            width: 50%;
                            display: flex;
                            flex-direction: column;

                            label {
                                font-family: $ff-text;
                                font-size: $fs-sm;
                                margin-bottom: $sp-xs;
                                color: $co-text;
                            }

                            input {
                                font-family: $ff-text;
                                font-size: $fs-sm;
                                border-radius: $br-sm;
                                border: $bo-xs;
                                padding: $sp-xs;
                                margin-bottom: $sp-sm;
                            }
                        }

                        .contact-right {
                            display: flex;
                            flex-direction: column;
                            width: 50%;

                            label {
                                font-family: $ff-text;
                                font-size: $fs-sm;
                                margin-bottom: $sp-xs;
                                color: $co-text;
                            }

                            textarea {
                                font-family: $ff-text;
                                font-size: $fs-sm;
                                padding: $sp-xs;
                                resize: none;
                                border-radius: $br-sm;
                                border: $bo-xs;
                            }
                        }
                    }

                    .bt-submit {
                        border: 1px solid $co-primary;
                        border-radius: 4px;
                        width: fit-content;
                        padding: $sp-xs $sp-sm;
                        background-color: $co-primary;
                        color: white;
                        cursor: pointer;
                        transition: transform $td-3, box-shadow $td-3;
                        box-shadow: $bs-sm;
                        font-family: $ff-text;
                        font-weight: $fw-semi;

                        @media (max-width: $bp-md) {
                            margin-top: $sp-md;
                            font-size: $fs-md;
                        }

                        .spinner {
                            width: 16px;
                            height: 16px;
                            border: 2px solid transparent;
                            border-top: 2px solid #fff;
                            border-radius: 50%;
                            animation: spin 1s linear infinite;
                            margin: 0 auto;
                        }

                        @keyframes spin {
                            0% {
                                transform: rotate(0deg);
                            }

                            100% {
                                transform: rotate(360deg);
                            }
                        }
                    }
                }
            }
        }
    }
}

.enviado-message {
    position: fixed;
    bottom: $sp-md;
    right: $sp-xl;
    background-color: $co-secondary;
    color: white;
    padding: $sp-xs $sp-md;
    border-radius: $br-sm;
    opacity: 1;
    transition: opacity $td-3 ease-in-out;
}

.enviado-message.show {
    opacity: 1;
}

@media screen and (max-width: $bp-sm) {
    section .contact .section-container .contact-form form .container-contact {
        flex-direction: column;

        .contact-left,
        .contact-right {
            width: 100%;
        }
    }
}