@import "../../styles/main.scss";

section {
    top: $he-xxl;
    margin: 0;
    background-color: $co-body-bg;
    flex-direction: column;

    .home {
        display: flex;
        margin-top: $he-xxl;
        height: calc(100vh - 290px);
        min-height: 300px;
        justify-content: space-between;

        @media (max-width: $bp-md) {
            height: auto;
            flex-direction: column-reverse;
            gap: 50px;
        }

        .home-header {
            display: flex;
            flex-direction: column;
            padding-left: $sp-xl - $sp-xs;

            @media (max-width: $bp-md) {
                padding-left: 0;
            }

            .header-name {
                font-size: $fs-xl;
                font-weight: $fw-semi;
                margin: 0;
                padding-left: 4px;
                color: $co-header;

                @media (max-width: $bp-md) {
                    font-size: $fs-xl;
                }
            }

            .header-title {
                white-space: nowrap;
                height: 110px;
                margin-top: 0;
                margin-bottom: $sp-md;
                background: linear-gradient(90deg, $co-primary, $co-text-title);
                background-clip: border-box;
                -webkit-text-fill-color: transparent;
                -webkit-background-clip: text;
                padding: 0;

                @media (max-width: $bp-md) {
                    margin-bottom: $sp-xs;
                }

                @media (max-width: $bp-sm) {
                    height: 80px;
                }

                @media (max-width: $bp-xs) {
                    height: 80px;
                    margin-bottom: $sp-xs;
                }
            }

            .header-subtitle {
                font-weight: $fw-semi;
                line-height: $sp-lg;
                margin-top: 0;
                margin-bottom: $sp-xl - $sp-xs;
                color: $co-header;

                @media (max-width: $bp-xs) {

                }
            }

            .header-description {
                color: $co-text;
                font-weight: $fw-semi;
                line-height: $lh-xl;
                max-width: 400px;
                margin: 0;

                @media (max-width: $bp-md) {

                }
            }
        }

        .home-photo {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;
            width: 40%;

            @media (max-width: $bp-md) {
                width: 100%;
            }

            .my-photo {
                position: absolute;
                z-index: 1;
                object-fit: contain;
                min-height: 200px;
                max-width: 380px;

                @media (max-width: $bp-md) {
                    position: relative;
                    max-width: fit-content;
                    width: 100%;
                }
            }

            .blob {
                position: absolute;
                overflow: hidden;
                max-width: 350px;
                width: 100%;
                height: 100%;
                max-height: 330px;
                min-height: 330px;
                border-radius: 42% 56% 72% 28% / 42% 42% 56% 48%;
                background-color: $co-primary;
                background-size: cover;
                background-position: center;
                animation: morph 3.75s linear infinite;

                @media (max-width: $bp-md) {
                    max-height: 450px;
                    max-width: 450px;
                    min-height: auto;
                }

                @media (max-width: $bp-xs) {
                    max-height: 350px;
                    max-width: 350px;
                    min-height: auto;
                }

                @keyframes morph {
                    0%,
                    100% {
                        border-radius: 42% 56% 72% 28% / 42% 42% 56% 48%;
                    }

                    33% {
                        border-radius: 72% 28% 48% 48% / 28% 28% 72% 72%;
                    }

                    66% {
                        border-radius: 100% 56% 56% 100% / 100% 100% 56% 56%;
                    }
                }
            }
        }
    }
}